import { Card, CardContent } from "@/components/ui/card"

export function CardsDashboard() {
  const metrics = [
    { label: "Net worth", value: "$4,012.40", color: "text-green-400" },
    { label: "Total rewards", value: "$0", color: "text-yellow-400" },
    { label: "Total assets", value: "$5,249.95", color: "text-blue-400" },
    { label: "Total debts", value: "$100", color: "text-red-400" },
  ]

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-6">
      {metrics.map((metric) => (
        <Card key={metric.label} className="bg-gray-800 border-gray-700">
          <CardContent className="p-4">
            <div className="text-gray-400 text-sm mb-1">{metric.label}</div>
            <div className={`text-2xl font-bold ${metric.color}`}>{metric.value}</div>
          </CardContent>
        </Card>
      ))}
    </div>
  )
}
